// import TripDisplay from "@/app/Components/TripDisplay/TripDisplay";
import React, { useState, useEffect } from "react";
import TripDisplay from "../../Components/TripDisplay/TripDisplay";
import useGettRequest from "../../Hooks/useGetRequest";
import usePostRequest from "../../Hooks/usePostRequest";
import Table from "../../Components/Table/Table";

function TripEnded({ section }) {
  const [tripList, setTripList] = useState([]);
  const [driversList, setDriversList] = useState([]);
  const [tripStateList, setTripStateList] = useState([]);
  const [trip, setTrip] = useState({});

  const [getTripList, loadingTripList] = usePostRequest(
    `${process.env.REACT_APP_TERA_URL + "back-office/trip/list-web"}`,
    setTripList,
    {
      trip_state:
        section === "NoAsigned"
          ? 1
          : section === "Completed"
          ? 6
          : section === "Canceled"
          ? 7
          : 4,
    }
  );

  const [getDriversList] = usePostRequest(
    `${process.env.REACT_APP_TERA_URL + "back-office/driver/list"}`,
    setDriversList,
    { driver_state: true }
  );

  const [getTripStateList] = useGettRequest(
    `${process.env.REACT_APP_TERA_URL + "back-office/trip-state/list"}`,
    setTripStateList
  );

  useEffect(() => {
    getTripList();
    getDriversList();
    getTripStateList();
  }, []);

  const TableSettings = {
    dense: "small",
    height: "450px",
    order: false,
    elevation: 2,
    selectionType: "single",
    header: [
      {
        align: "left",
      },
    ],
    body: [
      {
        checkbox: false,
        rowSelectable: false,
      },
    ],
  };

  const TableData = {
    header: [
      {
        label: "Pasajero",
        id: "customer",
      },
      {
        label: "Conductor",
        id: "driver",
      },
      {
        label: "Origen",
        id: "origin_address",
      },
      {
        label: "Ref Origen",
        id: "origin_ref",
      },
      {
        label: "Destino",
        id: "destination_address",
        // replaceData: {
        //   S: <CheckCircleIcon sx={{ color: "green" }} />,
        //   N: <RemoveIcon sx={{ color: "grey" }} />,
        // },
      },
      {
        label: "Ref Destino",
        id: "destination_ref",
      },
      {
        label: "Distancia",
        id: "distance",
        // tooltip: "Matriculados del grupo",
        // align: "center",
      },
      {
        label: "Precio",
        id: "driverInitialAmount",
        // tooltip: "Total de Matriculados con Transparencia",
        // align: "center",
      },
      {
        label: "Saldo",
        id: "saldo",
        // replaceCell: ({ cell, row, isItemSelected }) => {
        //   console.log(row);
        //   console.log(isItemSelected);
        //   return (
        //     <div
        //       style={{
        //         backgroundColor: cell > 20 ? "grey" : "green",
        //         minWidth: "100%",
        //         minHeight: "100%",
        //         height: "100%",
        //       }}
        //       className="flex justify-center items-center"
        //     >
        //       <label>{cell}</label>
        //     </div>
        //   );
        // },
      },
    ],
    dataKey: "pk",
    data: tripList?.map((trip) => trip.fields),
  };

  return (
    <div style={{ height: "70vh", overflow: "auto" }}>
      {/* {tripList.map((trip, index) => (
        <TripDisplay
          trip={trip?.fields}
          driversList={driversList?.map((driver) => driver.fields)}
          tripStateList={tripStateList}
          tripSection={section}
        />
      ))} */}
      <Table
        settings={TableSettings}
        data={TableData}
        selected={trip}
        setSelected={setTrip}
        loading={loadingTripList}
      />
    </div>
  );
}

export default TripEnded;
